@import url('https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,300;0,400;0,600;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Caprasimo&display=swap');
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    cursor: default;
  }
  
::-webkit-scrollbar {
    display: none;
}

html {
  scroll-behavior: smooth;
}

.linkActivate {
  text-decoration: none;
  color: rgba(255, 87, 51, 1);
  width: 100%;
  text-align: center;
  font-weight: bold;
}

.linkActivate:hover {
  color: #00c703;
  border-bottom: 4px solid #00c703;
  font-weight: 600;
}

.active-tab .linkActivate {
  color: #00c703;
  border-bottom: 4px solid #00c703;
  font-weight: 600;
}


.scroll-action {
  width: 100%;
  bottom: 0;
  position: fixed;
}

.layout {
  position: absolute;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: none;
}

.navbar-container {
  position: fixed;
  width: 100%;
  max-width: 1400px;
  height: 50px;
  display: flex;
  justify-content: space-between;
  margin-top: 37px;
  z-index: 10;
  cursor: pointer;
  font-weight: bold;
}

.nav-tab {
  width: 25%;
  display: flex;
  justify-content: center;
  align-content: center;
  font-size: 1.2rem;
  font-weight: 600;
  font-family: 'Source Code Pro', monospace;
}

.intro-typewriter {
  background: linear-gradient(to bottom, rgba(232, 218, 204, 1), 88%, rgba(232, 218, 204, 0));
  width: 100%;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;

  .typed-text {
    font-size: 5rem;
    border-bottom: 5.5px dotted rgba(0, 0, 0, 0.65);
    color: rgba(255, 87, 51, 1);
  }
  .cursor {
    font-size: 5rem;
    color: #00c703;
  }
  #terminal-sign {
    font-size: 5rem;
    color: rgba(0, 0, 0, 0.65);
  }
}

.intro-typewriter:hover {
  cursor: default;
}

#placeholder-1 {
  height: 30vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
  color: rgba(255, 87, 51, 1);
  border-color: black;
}

.roles-container {
  border-top: 3px solid rgba(255, 87, 51, 1);
  border-bottom: 3px solid rgba(255, 87, 51, 1);
  padding: 13px;
}

.change-text {
  
}

.plcHlrActive .roles-container {
  animation: fadeInFromNone 2s ease-out forwards, changeBorder 2.5s ease-out forwards;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  display: block;
}

@keyframes fadeInFromNone {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}

@keyframes changeBorder {
  0% {
    width: 100%;
  }
  100% {
    width: 130%;
  }
}

#placeholder-2 {
  height: 30vh;
}
#placeholder-3 {
  height: 50vh;
}

.projects-slider {
  height: 215vh;
  width: 100%;
  max-width: 1400px;
}

.projects-container {
  position: sticky;
  top: 0;
  width: 70%;
  height: 100vh;
  display: flex;
  justify-content: center;
  margin-left: 30%;
  align-items: center;
  opacity:0;
}

.projectsActive .projects-container {
  animation: fadeInFromNone 0.75s ease-out forwards;
}

.about-me-slider {
  width: 100%;
  height: 185vh;
  max-width: 1400px;
}

.about-me-container {
  position: sticky;
  top: 0;
  width: 70%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 30%;
  border-radius: 40px;
  opacity:0;
}

.aboutActive .about-me-container {
  animation: fadeInFromNone 0.75s ease-out forwards;
}

.contact-me {
  height: 100vh;
  width: 100vw;
  background-color: rgba(232, 218, 204, 1);
  display: flex;
  justify-content: center;
}

.desktop-projects {
  .img-base {
    border: 3px solid white;
  }
  .project-index-container {
    border-left: 2px solid white;
  }
  .project-index-tag {
    border-bottom: 1.5px solid white;
    border-top: 1.5px solid white;
    border-right: 1.5px solid white;
    color: white;
  }
  .heading-text-project-mobile {
    color: white;
  }
  .button-tray-project-mobile {
    border-top: 3px dotted white;
  }
  .projectURLButton {
    border: 0;
  }
  .projectURLButton:hover {
    border: 2px dashed white;
  }
  .description-hide {
    color: white;
    border: 3px solid white;
  }
}

.desktop-abouts {
  .img-base {
    border: 3px solid white;
  }
  .about-index-container {
    border-right: 2px solid white;
  }
  .about-index-tag {
    border-bottom: 1.5px solid white;
    border-top: 1.5px solid white;
    color: white;
  }
  .heading-text-about-mobile {
    color: white;
  }
  .button-tray-about-mobile {
    border-top: 3px dotted white;
  }
  .projectURLButton:hover {
    border: 2px dashed white;
  }
  .about-slider-base-mobile {
    #description-hide-about-id {
      color: white;
      border: 3px solid white;
    }
  }
}

@media screen and (max-width: 850px) {
  .intro-typewriter {
    .typed-text {
      font-size: 2.5rem;
    }
    .cursor {
      font-size: 2.5rem;
    }
    #terminal-sign {
      font-size: 2.5rem;
    }
  }
  .nav-tab {
    font-size: 1rem;
  }
  .projects-container {
    justify-content: center;
  }
  .about-me-container {
    justify-content: center;
  }
}

@media screen and (max-width: 600px) {
  .intro-typewriter {
    padding: 0;
    margin: 0;
    .typed-text {
      font-size: 1rem;
      border: 1.5px solid black;
    }
    .cursor {
      font-size: 1.5rem;
    }
    #terminal-sign {
      font-size: 1.5rem;
    }
  }
}
